    </div>
    <div style="height:30px;line-height:30px;">总公司二级机构预算</div>
    <div class="data">
      <div class="table">
        <base-table
          :columns="columns"
          :data="splitList">
        </base-table>
        <div style="height: 30px;line-height: 30px;">拆分三级机构</div>
        <base-table
          :columns="splitColumns"
          :showHeader="false"
          :data="subCompanyList"
          :showSummary="true"
          :summaryMethod="getSummaries">
        </base-table>
      </div>
      <div class="option">
        <el-button plain>下载预算模板</el-button>
        <el-button plain>上载预算模板</el-button>
        <el-button plain @click="save">保存</el-button>
        <el-button plain >校验</el-button>
        <el-button plain @click="approval">提交审批</el-button>
