        {prop: 'targetPremium', label: '目标保费 (万元)', width: 140},
        {prop: 'targetOperaCostRate', label: '目标经营成本率', width: 140},
        {prop: 'targetReimburseRate', label: '目标赔付率', width: 140},
        {prop: 'targetMarketCostRate', label: '目标市场费用率', width: 140},
        {prop: 'targetNonMarketCostRate', label: '目标非市场费用率', width: 140}
      ],
      splitColumns: [
        {prop: 'subInstitution', label: '机构', width: 160},
        {prop: 'type', label: '预算类型', width: 80},
        {prop: 'channel', label: '预算渠道', width: 100},
        {prop: 'timeSlot', label: '预算时间段', width: 100},
        {prop: 'bore', label: '预算口径', width: 100},
        {prop: 'productType', label: '业务分类', width: 100},
        {prop: 'targetPremium', label: '目标保费 (万元)', width: 140, isEdit: true, type: 'input'},
        {prop: 'targetOperaCostRate', label: '目标经营成本率', width: 140, isEdit: true, type: 'input'},
        {prop: 'targetReimburseRate', label: '目标赔付率', width: 140, isEdit: true, type: 'input'},
        {prop: 'targetMarketCostRate', label: '目标市场费用率', width: 140, isEdit: true, type: 'input'},
        {prop: 'targetNonMarketCostRate', label: '目标非市场费用率', width: 140, isEdit: true, type: 'input'}
      ],
      splitList: [
        {
          subInstitution: '山西省分公司',
          type: '年度',
          channel: '非网电预算',
          timeSlot: '2018',
          bore: '保单年口径',
          productType: '全部',
