          bore: '保单年口径',
          productType: '全部',
          targetPremium: '',
          targetOperaCostRate: '',
          targetReimburseRate: '',
          targetMarketCostRate: '',
          targetNonMarketCostRate: ''
        },
        {
          subInstitution: '晋城支公司',
          type: '年度',
          channel: '传统渠道',
          timeSlot: '2018',
          bore: '保单年口径',
          productType: '全部',
          targetPremium: '',
